import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import { publicityList,publicity } from '@/static/js/api.js'
export default {
	components: {
		uniLoadMore
	},
	data(){
		return{
			resdetail:{
				pageNo:1,
				detailheader:{
					amtTotal:0,
					amtPaid:0,
					finishCount:0
				},
				detaillist:{
					list:[
						{
							amtAssert:"",
							amtShare:"",
							customerId:"",
							customerPN:"",
							eventNo:"",
							level:"",
							licensePlateNumber:"",
							model:"",
							nameCarOwner:"" 
						}
					]
				}
			},
			loadingText: '加载中...',
			loadingType: 0,
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}

		}
	},
	onShow(){
		this.api()
	},
	onLoad: function (options) {
		
		uni.startPullDownRefresh();
	},
	methods:{
		api:function(){
			
			publicityList({
				pageNo:this.resdetail.pageNo,
				flag:"2",
			}).then((res)=>{
				if(res.code==="200"){
					this.resdetail.detaillist = res.data
				}else{
					uni.showToast({
						icon:"none",
					    title: res.message,
					    duration: 2000
					});
				}
				
			})
			publicity({
				
			}).then((res)=>{
				if(res.code==="200"){
					this.resdetail.detailheader = res.data
				}else{
					uni.showToast({
						icon:"none",
					    title: res.message,
					    duration: 2000
					});
				}
			})
		},
		navigateto:function(eventNo){
			uni.navigateTo({
				url:"/pages/talk/talkDetail/index?eventNo="+eventNo 
			})
		},
	},
	onPullDownRefresh:function() {
		this.resdetail.pageNo =1
		this.api()
	},
	onReachBottom: function() {
		uni.showLoading({
		    title: '加载中'
		});
		this.resdetail.pageNo++
		publicityList({
			pageNo:this.resdetail.pageNo,
			flag:"2",
		}).then((res)=>{
			if(res.code==="200"){
				res.data.list.map(item=>{
					this.resdetail.detaillist.list.push(item)
				})
			}
			uni.hideLoading();
		})
		
	}
}
